@import url('https://fonts.googleapis.com/css?family=Open+Sans:300');
@import url('https://fonts.googleapis.com/css?family=Roboto:300');

.App {
  text-align: center;
  background: orange;
  background: -webkit-gradient(linear, left top, right bottom, from(rgb(0, 127, 211)), to(rgb(255, 0, 255)));
  background: -webkit-linear-gradient(top left, rgb(0, 127, 211), rgb(255, 0, 255));
  background: -o-linear-gradient(top left, rgb(0, 127, 211), rgb(255, 0, 255));
  background: linear-gradient(to bottom right, rgb(0, 127, 211), rgb(255, 0, 255));
  color: white;
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Open Sans';
  font-size: 2em;
}




